.modal-dialog {
	max-width: 660px;
	display: flex;
	align-items: center;
	justify-content: center;
	margin: 20px auto;
}

.modal-content {
	padding: 15px 11px 45px 0px;
	background-color: #f4f6fa;
	border-radius: 14px;
	border: none;
}

.adding_res {
	padding: 10px 60px 0px 60px;
}

.adding_res h6 {
	font-size: 16px;
	line-height: 1.38;
	border-radius: 9px;
	background-color: #fff;
	color: rgba(58,74,102,0.5);
	padding: 10px 15px;
	margin-top: 10px;
}

.adding_res h5 {
	font-size: 16px;
	font-weight: bold;
	line-height: 1.38;
	color: #3a4a66;
	padding-top: 40px;
	padding-bottom: 10px;
}

.adding_res .row {
	margin: 0px -5px;
}

.adding_res .col-sm-6 {
	padding: 5px;
}

.list_res {
	padding: 16px 32px 13px;
	border-radius: 14px;
	background-color: #fff;
	text-align: center;
}

.list_res span {
	width: 42px;
	height: 42px;
	padding: 10px;
	border-radius: 6px;
	box-shadow: 0 10px 20px 0 rgba(0,0,0,0.1);
	background-color: #fff;
	display: block;
	margin: 0px auto;
}

.list_res p {
	font-size: 16px;
	line-height: 1.31;
	color: #3a4a66;
	padding-top: 20px;
}

.white_strip {
	padding: 10px 10px 10px 25px;
	border-radius: 9px;
	background-color: #fff;
	margin-top: 20px;
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.white_strip .catalog_con img {
	width: 100px;
	border-radius: 10px;
	margin-left: 20px;
	position: relative;
	z-index: 1;
}

.white_strip .catalog_con .catalog_img_box::before {
	content: '';
	width: 100px;
	height: 70px;
	background-color: #0af;
	opacity: 0.1;
	position: absolute;
	right: -5px;
	top: 4px;
	border-radius: 10px;
}

.white_strip .catalog_con .catalog_img_box::after {
	content: '';
	width: 100px;
	height: 70px;
	background-color: #0af;
	opacity: 0.1;
	position: absolute;
	right: -5px;
	top: 4px;
	border-radius: 10px;
	right: -10px;
	top: 7px;
}

.checkmark {
	width: 42px;
	height: 42px;
	padding: 7px;
	border-radius: 14px;
	box-shadow: 0 10px 20px 0 rgba(0,0,0,0.1);
	background-color: #0af;
	display: flex;
	align-items: center;
	justify-content: center;
}

.white_con p {
	font-size: 16px;
	color: rgba(58,74,102,0.5);
}

.white_con p i {
	margin-right: 72px;
}

.change_text img {
	display: none;
}

.plan_btn {
	font-size: 14px;
	line-height: 1.29;
	color: #3a4a66;
	padding: 0px;
	border: 0px;
	outline: none;
	background: transparent;
}

.plan_btn:hover {
	opacity: 0.8;
}

.blue_btn {
	border-radius: 19px;
	background-color: rgba(0,170,255,0.1);
	font-size: 14px;
	font-weight: bold;
	color: #0af;
	padding: 10px 30px 10px 30px;
	margin-right: 30px;
	min-width: 130px;
}

.blue_btn:hover {
	opacity: 0.8;
}

.checkdiv {
	padding: 5px 0px 15px;
}

.link_strip {
	border-radius: 9px;
	background-color: #fff;
	margin-top: 20px;
}

.link_strip a {
	display: inline-flex;
	font-size: 16px;
	color: #0af;
	align-items: center;
}

.link_strip span {
	width: 42px;
	height: 42px;
	padding: 10px;
	border-radius: 6px;
	box-shadow: 0 10px 20px 0 rgba(0,0,0,0.1);
	background-color: #fff;
	margin-left: 20px;
	display: inline-block;
	z-index: 9;
	position: relative;
}

.link_strip .green_btn {
	color: #4de442;
}

.link_strip .green_btn img {
	margin-left: 10px;
}

.link_strip2 {
	background: #fff;
	width: 80%;
}

.link_strip2 p {
	font-size: 16px;
	color: rgba(58,74,102,0.5);
	line-height: 1.38;
	display: inline-block;
}

.green_btn {
	background-color: rgba(17,255,0,0.1);
	display: none;
	font-weight: normal;
}

#subjectname .grade_btn {
	margin-top: 20px;
}

#textpop p {
	line-height: 1.5;
	padding: 30px 0px;
}

.plan_text p {
	font-size: 14px;
	line-height: 1.29;
	color: rgba(58,74,102,0.5);
	padding-top: 20px;
}

.nameoffile {
	font-size: 14px;
	padding-left: 16px;
	color: #3a4a66;
}

#AddstudentsToGroup .modal-content {
	padding: 15px 11px 30px 0px;
}

.student_list {
	height: 380px;
	border-radius: 9px;
	background-color: #fff;
	overflow: scroll;
	margin-top: 30px;
}

.student_list::-webkit-scrollbar {
	display: none;
}

.student_list li {
	font-size: 16px;
	line-height: 2.63;
	color: #3a4a66;
	display: flex;
	align-items: center;
	border-bottom: solid 1px rgba(209,215,228,0.5);
	padding: 5px 40px 5px 27px;
	justify-content: space-between;
	cursor: pointer;
}

.student_list li:last-child {
	border-bottom: 0px;
}

.student_list li img {
	opacity: 0;
	visibility: hidden;
	width: 15px;
}

.student_list li:hover {
	border-radius: 9px;
	background-color: #e5f6ff;
}

.student_list li:hover img {
	opacity: 1;
	visibility: visible;
}

.student_list li.active {
	border-radius: 9px;
	background-color: #e5f6ff;
}

.student_list li.active img {
	opacity: 1;
	visibility: visible;
}

.taskgoing_list {
	height: 528px;
	border-radius: 9px;
	background-color: #fff;
	margin-top: 13px;
	overflow-y: scroll;
}

.taskgoing_list::-webkit-scrollbar {
	display: none;
}

.taskgoing_list li {
	font-size: 16px;
	line-height: 2.63;
	color: #3a4a66;
	display: flex;
	align-items: center;
	border-bottom: solid 1px rgba(209,215,228,0.5);
	padding: 5px 40px 5px 27px;
	justify-content: space-between;
	cursor: pointer;
}

.taskgoing_list li:last-child {
	border-bottom: 0px;
}

.taskgoing_list li i {
	color: rgba(58,74,102,0.5);
	width: 50%;
}

.taskgoing_list li span {
	width: 50%;
}

.taskgoing_list li img {
	width: auto;
}

.taskgoing_list li.active {
	background-color: #e5f6ff;
}

.add .link_strip2 {
	width: 100%;
}

.add .link_strip2 p {
	width: 100%;
}

.add .link_strip2 p i {
	float: left;
	padding-left: 15px;
}

#taskongoing p {
	font-size: 14px;
	color: #3a4a66;
}

#taskongoing .modal-content {
	padding: 15px 11px 0px 0px;
}

.taskgoing_head {
	padding-top: 7px;
}

.taskgoing_head p span i {
	color: rgba(58,74,102,0.5);
	margin-left: 5px;
}

.moadlpage a {
	display: block;
	margin: 10px 0px;
	width: max-content;
}

#alert .modal-dialog {
	width: 430px;
}

#alert .adding_res.text-center p {
	padding: 10px 0px 30px;
}

.classdrop {
	position: relative;
	cursor: pointer;
	display: block;
}

.classdrop ul {
	display: none;
	position: absolute;
	background: #fff;
	width: 100%;
	right: 0px;
	border-radius: 9px;
	padding: 0px 10px 10px 25px;
	z-index: 2;
	top: 33px;
}

.classdrop ul li {
	display: block;
	font-size: 14px;
	line-height: 1.57;
	color: #3a4a66;
	cursor: pointer;
}

.classdrop ul li:hover {
	color: #0af;
}

.disabledcontent {
	opacity: 0.5;
}

.affiliation_students {
	width: 100%;
	top: 38px;
	padding: 0px 0px 10px 0;
	box-shadow: none;
	border-top-left-radius: 0;
	border-top-right-radius: 0;
}

.affiliation_students ul {
	padding: 10px 10px;
}

.affiliation_students ul:first-child {
	border-bottom: 1px solid #d1d7e4;
	padding-top: 0px;
}

.affiliation_students ul:first-child li {
	color: #9ca4b2;
}

.affiliation_students ul:first-child li:hover {
	color: #9ca4b2;
}

.affiliation_students ul:first-child li:hover span {
	color: #00aaff;
}

.affiliation_students ul:first-child li span {
	pointer-events: auto;
	color: #3a4a66;
}

.affiliation_students ul:first-child li span:hover {
	color: #00aaff;
}

.affiliation_students ul li {
	display: block;
}

.students_groups {
	margin: 10px 0px 30px 0;
	padding: 10px 15px 15px;
	border-radius: 9px;
	background-color: #e1e3ea;
}

.students_groups .student_list {
	margin-top: 10px;
	height: 182px;
}

.students_groups .student_list P {
	padding-right: 20px;
	line-height: 30px;
	opacity: 0.5;
}

.students_groups .student_list li {
	line-height: 32px;
	padding: 5px 28px 5px 27px;
}

.students_groups .check {
	margin-left: 12px;
	background-color: #fff;
}

.checkbox {
	margin-top: 12px !important;
}

.division_groups {
	padding-bottom: 0px;
}

.division_groups .form-control {
	padding-right: 200px;
}

.division_groups .adding_res {
	padding: 5px 0px 7px;
}

.division_groups .adding_res .wrap_in {
	position: relative;
}

.division_groups .adding_res .wrap_in span {
	position: absolute;
	right: 15px;
	font-size: 16px;
	font-weight: normal;
	font-stretch: normal;
	font-style: normal;
	line-height: 1.38;
	letter-spacing: normal;
	text-align: right;
	color: #3a4a66;
	top: 0;
	bottom: 0;
	margin: auto;
	height: 21px;
	opacity: 0.5;
}

#addtask1 .modal-content {
	padding: 15px 11px 95px 0px;
}

#addtask1 .modal-content .input-group-addon {
	position: relative;
	margin-top: 181px;
	height: 1px;
	overflow: hidden;
}

#addtask1 .modal-content .date {
	position: relative;
}

#addtask1 .modal-content .date .form-control {
	position: relative;
	direction: ltr;
	text-align: right;
	top: 68px;
}

#addtask1 .modal-content .white_strip {
	overflow: hidden;
	height: 40px;
	position: relative;
}

#addtask1 .modal-content .white_strip label {
	width: 500px;
	padding-right: 10px;
	margin-bottom: 0;
	line-height: 40px;
	font-size: 16px;
	color: rgba(58,74,102,0.5);
}

.affiWrite {
	color: #3a4a66;
}

.btn-file {
	position: relative;
	overflow: hidden;
}

.btn-file input[type=file] {
	position: absolute;
	top: 0;
	right: 0;
	min-width: 100%;
	min-height: 100%;
	font-size: 100px;
	text-align: right;
	filter: alpha(opacity=0);
	opacity: 0;
	background: red;
	cursor: inherit;
	display: block;
	pointer-events: none;
}

.link_strip {
	position: relative;
	transition: 0.2s ease all;
}

.link_strip:hover {
	opacity: 0.7;
}

.link_strip .input-group {
	position: absolute;
	top: 0;
	right: 0;
}

.link_strip .input-group .btn-file {
	position: absolute;
	width: 100%;
	height: 100%;
	margin: 0;
	cursor: pointer;
}

.link_strip .input-group span {
	background-color: transparent;
	padding: 0;
	border: none;
	border-radius: 0;
	box-shadow: none;
	margin: 0px;
	width: 50%;
	line-height: 40px;
}

.link_strip .input-group span.file-input-label {
	text-align: left;
	font-size: 14px;
	color: #3a4a66 !important;
	padding-left: 12px;
}

button {
	cursor: pointer;
}

input[type="date"] {
	text-align: right;
}

input[type="date"]::-webkit-inner-spin-button {
	display: none;
	-webkit-appearance: none;
}

input[type="date"]::-webkit-calendar-picker-indicator {
	display: none;
	-webkit-appearance: none;
}

.modal.fade .modal-dialog {
	-webkit-transform: translateY(-5%) !important;
	-ms-transform: translateY(-5%) !important;
	transform: translateY(-5%) !important;
}

.modal.show .modal-dialog {
	-webkit-transform: translateY(0) !important;
	-ms-transform: translateY(0) !important;
	transform: translateY(0) !important;
}

